.humburger-menu__container {
  display: none;
}

@media screen and (max-width: 767px) {
  .humburger-menu__container {
    display: flex;
    flex-direction: column;
    gap: 18px;
    align-items: center;
    position: absolute;
    padding-top: 40px;
    width: 100%;
    top: 0px;
  }
}
